<template>
    <div class="safety">
        <van-nav-bar title="安全中心" left-text="返回" left-arrow @click-left="$router.go(-1)" />

        <div class="list-box">
            <van-button type="default" :block="true" @click="isShow = true">修改密码</van-button>
            <van-button type="default" :block="true" @click="destroy">注销账号</van-button>
        </div>

        <div class="logout">
            <van-button :round="true" :block="true" type="info" @click="logout">退出登录</van-button>
        </div>
        
        <van-popup v-model="isShow" round closeable position="bottom">
            <h3 class="title">修改密码</h3>

            <van-form @submit="onSubmit">
                <van-field
                    v-model="pwd.oldPassword"
                    name="旧密码"
                    label="旧密码"
                    placeholder="旧密码(6-16位)"
                />
                <van-field
                    v-model="pwd.password"
                    type="text"
                    name="新密码"
                    label="新密码"
                    placeholder="新密码(6-16位)"
                />
                <div style="margin: 16px;">
                    <van-button round block type="info" native-type="submit">
                    提交
                    </van-button>
                </div>
            </van-form>
        </van-popup>

    </div>
</template>

<script>
    import { Toast } from "vant";
    import { Dialog } from "vant";
    import validForm from "../assets/js/validForm";

    export default {
        data() {
            return {
                isShow: false,

                pwd: {
                    oldPassword: '',
                    password: '',
                },

            };
        },

        methods: {
            // 提交修改密码
            onSubmit() {
                if(!validForm.validateForm(this.pwd)) {
                    return;
                }

                // 获取token
                let tokenString = localStorage.getItem("Tk");

                // 开启加载提示
                Toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭, 如果该值为0, 则不会自动关闭
                    duration: 0,
                });

                this.axios({
                    method: "POST",
                    url: "/updatePassword",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        password: this.pwd.password,
                        oldPassword: this.pwd.oldPassword,
                    }
                }).then(res => {
                    // 关闭加载提示
                    Toast.clear();

                    this.isShow = false;

                    if(res.data.code == "E001") {
                        Toast("修改成功");

                        //清除token
                        localStorage.removeItem("Tk");

                        this.$router.push({ name: "Login" });
                        
                    }else {
                        Toast(res.data.msg);
                    }

                }).catch(err => {
                    // 关闭加载提示
                    Toast.clear();


                });
            },

            // 退出登录
            logout() {
                // 清除token
                localStorage.removeItem("Tk");

                this.$router.push({ name: "Login" });
            },

            // 注销账号
            destroy() {
                Dialog.confirm({
                    title: "注销账号",
                    message: "警告: 注销后数据不可恢复!",
                }).then(() => {
                    // 获取token
                    let tokenString = localStorage.getItem("Tk");

                    // 开启加载提示
                    Toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        //延迟自动关闭, 如果该值为0, 则不会自动关闭
                        duration: 0,
                    });

                    this.axios({
                        method: "POST",
                        url: "/destroyAccount",
                        data: {
                            appkey: this.appkey,
                            tokenString,
                        }
                    }).then(res => {
                        // 关闭加载提示
                        Toast.clear();

                        if(res.data.code == "G001") {
                            Toast(res.data.msg);

                            //清除token
                            localStorage.removeItem("Tk");

                            this.$router.push({ name: "Login" });
                            
                        } else {

                            Toast(res.data.msg);

                        }

                    }).catch(err => {
                        // 关闭加载提示
                        Toast.clear();


                    });
                }).catch(() => {

                });
            },
        },
    }
</script>

<style lang="less" scoped>
    .safety{
        color: #646566;
    }

    .list-box{
        padding: 0 30px;

        .van-button{
            height: 40px;
            margin-top: 30px;
        }
    }

    .logout{
        margin-top: 30px;
        padding: 30px;
    }

    .van-popup{
        padding: 10px;
        box-sizing: border-box;


        .title{
            line-height: 40px;
            text-align: center;
            font-size: 16px;
        }
    }
</style>